*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 10px; // 10px default
}

body {
  box-sizing: border-box;
  font-family: "Public Sans", sans-serif;
  font-family: "Barlow", sans-serif;
  // font-family: "Fraunces", serif;

  min-width: 35rem;
}
